import { useHistory } from "react-router";
import { useSetRecoilState } from "recoil";
import { CheckBox } from "src/components/button/checkBox";
import { EditButton } from "src/components/button/editButton";
import { artistTriggerState } from "src/models/artistProfile/profileBanner";
import {
    IUser,
    usePostSignature,
    usePostUserCanSee,
} from "src/models/userManagement/userManagement";
import { makeCenter, SpoqaHansSansNeo } from "src/styles/css";
import styled from "styled-components";

export const VUser = ({
    createdAt,
    email,
    emailPermit,
    phoneNumber,
    realName,
    textPermit,
    smallProfileURI,
    userType,
    nameForURI,
    signature,
    userCanSee,
}: IUser) => {
    const history = useHistory();
    const setArtistTriger = useSetRecoilState(artistTriggerState);
    const [fetch] = usePostSignature();
    const [fetch2] = usePostUserCanSee();

    return (
        <Container>
            <Profile src={smallProfileURI} />
            <Text style={{ width: 130 }}>{realName}</Text>
            <Text style={{ width: 70 }}>{userType}</Text>
            <Text style={{ width: 270 }}>{email}</Text>
            <Text style={{ width: 150 }}>{phoneNumber}</Text>
            <Text style={{ width: 60 }}>{emailPermit ? "Y" : "N"}</Text>
            <Text style={{ width: 60 }}>{textPermit ? "Y" : "N"}</Text>
            <Text style={{ width: 120 }}>{createdAt.split("T")[0]}</Text>
            <Text style={{ width: 70 }}>
                <CheckBox
                    check={signature}
                    onClick={() => fetch(!signature, email)}
                />
            </Text>{" "}
            <Text style={{ width: 70 }}>
                <CheckBox
                    check={userCanSee}
                    onClick={() => fetch2(!userCanSee, email)}
                />
            </Text>
            <EditButton
                rightAlign={true}
                onClick={() => {
                    setArtistTriger(Date.now());
                    history.push(`/${nameForURI}`);
                }}
            ></EditButton>
        </Container>
    );
};
const Profile = styled.img`
    width: 48px;
    height: 48px;
`;
const Text = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)}
    ${makeCenter()}
`;
const Container = styled.div`
    ${makeCenter(0, 1)}
    margin-top: 16px;
    width: 1156px;
    height: 48px;
    display: flex;
`;
