import { useState } from "react";
import { Button } from "src/components/button/button";
import { EditButton } from "src/components/button/editButton";
import { SpoqaHansSansNeo } from "src/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import { InputForm } from "../../components/input/inputForm";
import { Modal } from "../../components/modal/modal";
import { useFetchNameEN } from "../../models/artistProfile/profileBanner";

export const UpdateNameEN = () => {
    const [isOpen, setIsOpen] = useState(false);
    const [fetchNameEN] = useFetchNameEN();
    const [input, setInput] = useState("");
    const onChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setInput(e.target.value);
    };

    return (
        <>
            <EditButton leftMargin={10} onClick={() => setIsOpen(true)} />
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <Header>{"영문이름 수정"}</Header>
                        <Main>
                            <InputForm
                                onKeyPress={() => {}}
                                style={{ marginBottom: 48 }}
                                onChange={onChange}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={input}
                                placeholder="한국 시스터즈의 계보를 잇는 여성 듀오의 유쾌한 위로"
                                header="영문이름"
                            ></InputForm>
                        </Main>
                        <Footer>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => fetchNameEN(input)}
                            >
                                {"확인"}
                            </Button>
                        </Footer>
                    </>
                )}
            </Modal>
        </>
    );
};

const Header = styled.div`
    position: relative;
    height: 72px;
    padding: 24px;
    ${SpoqaHansSansNeo(20, 24, 700)};
    display: flex;
    align-items: center;
`;

const Main = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    border-top: 1px solid #828282;
    min-height: 150px;
`;

const Footer = styled.div`
    width: 100%;
    margin-top: auto;
    border-top: 1px solid #828282;
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    display: flex;
`;
