import axios, { AxiosRequestConfig } from "axios";
import { useState } from "react";
import { useHistory } from "react-router";
import { Button } from "src/components/button/button";
import { InputForm } from "src/components/input/inputForm";
import { API_SERVER2 } from "src/constants";
import { onChange } from "src/lib/lib";
import { makeCenter } from "src/styles/css";
import styled from "styled-components";

export const signIn = async (
    history,
    email,
    password,
    emailError,
    passwordError,
) => {
    let params = new URLSearchParams();
    const url = API_SERVER2 + `/auth/signin/local`;
    params.append("email", email);
    params.append("password", password);
    const options: AxiosRequestConfig = {
        method: "POST",
        data: params,
        url: url,
        withCredentials: true,
    };

    axios(options)
        .then(res => {
            if (res.status === 200) {
                window.localStorage.setItem("isLogin", "true");
                window.localStorage.setItem(
                    "userData",
                    JSON.stringify(res.data),
                );
                history.push("/usermanagement");
            }
        })
        .catch(error => {
            const errorCode = error.response.data.code;

            if (errorCode === 100006) {
                emailError();
            } else if (errorCode === 100005) {
                passwordError();
            }
        });
};

export function LoginSection() {
    const history = useHistory();
    const [requirements, setRequirements] = useState([
        {
            id: "email",
            activate: false,
        },
        {
            id: "password",
            activate: false,
        },
    ]);

    const [inputEmail, setInputEmail] = useState("");
    const [inputPassword, setInputPassword] = useState("");

    const setRequirement = (id, activate) => {
        setRequirements(
            requirements.map(requirement =>
                requirement.id === id
                    ? { ...requirement, activate: activate }
                    : requirement,
            ),
        );
    };

    const handleKeyPress: React.KeyboardEventHandler<
        HTMLInputElement | HTMLTextAreaElement
    > = (e: React.KeyboardEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        if (e.key === "Enter") {
            signIn(
                history,
                inputEmail,
                inputPassword,
                () => setRequirement("email", true),
                () => setRequirement("password", true),
            );
        }
    };
    return (
        <Container>
            <InnerContainer>
                <InputForm
                    onKeyPress={handleKeyPress}
                    marginBottom={48}
                    height={84}
                    onChange={onChange(setInputEmail)}
                    error={requirements[0].activate}
                    errorMessage={"*등록되지않은 이메일입니다."}
                    name="email"
                    value={inputEmail}
                    placeholder="example@example.com"
                    header={"Email Address"}
                />
                <InputForm
                    onKeyPress={e => handleKeyPress(e)}
                    marginBottom={48}
                    height={84}
                    onChange={onChange(setInputPassword)}
                    error={requirements[1].activate}
                    errorMessage={"*비밀번호를 다시 확인해주세요."}
                    value={inputPassword}
                    placeholder="영문, 숫자, 특수문자 포함 8자 이상"
                    header="Password"
                    type="password"
                />

                <Button
                    onClick={() =>
                        signIn(
                            history,
                            inputEmail,
                            inputPassword,
                            () => setRequirement("email", true),
                            () => setRequirement("password", true),
                        )
                    }
                    activate={true}
                    width={372}
                    height={48}
                >
                    LOG IN
                </Button>
            </InnerContainer>
        </Container>
    );
}

const Container = styled.div`
    margin-top: 80px;
    ${makeCenter(1, 0)}
`;
const InnerContainer = styled.div`
    padding: 80px 195px;
    width: 764px;
    border: 1px solid #f7f7f7;
    border-radius: 8px;
`;
