import { useEffect } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { useRecoilState } from "recoil";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "./constants";
import { ModalManager } from "./lib/modalManager";
import { currentSizeSelector } from "./models/currentSize";
import { ArtistProfile } from "./views/artistProfile/artistProfile";
import { PortfolioDetail } from "./views/artistProfile/portfolioDetail";
import { Login } from "./views/login/login";
import { UserManagement } from "./views/userManagement/userManagement";

function App() {
    const [currentSize, setCurrentSize] = useRecoilState(currentSizeSelector);
    const culSize = () => {
        if (window.innerWidth >= BREAKPOINT_LARGE) {
            return "LARGE";
        } else if (window.innerWidth >= BREAKPOINT_MIDDLE) {
            return "MIDDLE";
        } else if (window.innerWidth >= BREAKPOINT_SMALL) {
            return "SMALL";
        } else {
            return "MOBILE";
        }
    };
    useEffect(() => {
        setCurrentSize(culSize());
    }, []);
    useEffect(() => {
        window.addEventListener("resize", () => setCurrentSize(culSize()));
        return () => {
            window.removeEventListener("resize", () =>
                setCurrentSize(culSize()),
            );
        };
    }, [currentSize]);
    return (
        <>
            <Router>
                <Switch>
                    <Route exact path="/" render={() => <Login />} />
                    <Route
                        exact
                        path="/portfoliodetail/:portfolioID"
                        render={() => (
                            <PortfolioDetail currentSize={currentSize} />
                        )}
                    />
                    <Route
                        path="/usermanagement"
                        render={() => <UserManagement />}
                    />
                    <Route
                        path="/:nameForURI/"
                        render={() => <ArtistProfile />}
                    />
                </Switch>
            </Router>
            <ModalManager />
        </>
    );
}
export default App;
