export interface portfolioData {
    files: File | null;
    name: string;
    withStage8: boolean;
    participants: string;
    category: string;
    year: string;
    genre: string;
    client: string;
    description: string;
    nameForURI: string;
}

export const artistTriggerState = atom({
    key: "artistTriggerState",
    default: Date.now(),
});

export const useSetArtistBanner = () => {
    const setArtistbannerState = useSetRecoilState(artistTriggerState);
    const nameForURI = getNameForURIFromURI();

    const updateArtistBanner = useCallback(file => {
        if (file === null) return;

        const data = new FormData();
        data.append("files", file);
        const params = {
            url: `/artists/${nameForURI}/banner`,
            data: data,
        };

        const options: AxiosRequestConfig = {
            method: "PATCH",
            data: data,
            url: API_SERVER2 + String(params.url),
            withCredentials: true,
        };
        axios(options).then(() => setArtistbannerState(Date.now()));
    }, []);

    return [updateArtistBanner];
};

