import React, { useEffect, useRef, useState } from "react";
import pause2 from 'src/images/pause2.svg';
import play2 from 'src/images/play2.svg';
import volumeOff from 'src/images/volumeOff.svg';
import volumeOn from 'src/images/volumeOn.svg';
import { makeCenter, SpoqaHansSansNeo } from "src/styles/css";
import styled from "styled-components";
import WaveSurfer from 'wavesurfer.js';


function format(seconds) {
    const date = new Date(seconds * 1000)
    const hh = date.getUTCHours()
    const mm = pad(date.getUTCMinutes())
    const ss = pad(date.getUTCSeconds())
    if (hh) {
        return `${hh}:${pad(mm)}:${ss}`
    }
    return `${mm}:${ss}`
}

function pad(string) {
    return ('0' + string).slice(-2)
}

function Duration({ className, seconds }) {
    return (
        <time dateTime={`P${Math.round(seconds)}S`} className={className}>
            {format(seconds)}
        </time>
    )
}
export const runTime = time => {
    let min = String(Math.floor(time / 60));
    let sec = String(time % 60);
    let runtime = "";

    if (min.length === 1) {
        min = "0" + min;
    }

    if (sec.length === 1) {
        sec = "0" + sec;
    }

    runtime = min + ":" + sec;

    return runtime;
};

export function MusicPlayer({ selectedMusic, toggle, isPlay }) {
    console.log('selectedMusic:', selectedMusic)
    const waveform = useRef();
    const volumeForm = useRef();
    const [toggleVolumeBtn, setToggleVolumeBtn] = useState(true);
    const [realTime, setRealTime] = useState(false);
    const [finished, setFinished] = useState(false);
    const [endTime, setEndTime] = useState('00:00');
    const [wavesurfer, setWavesurfer] = useState(null);
    const [volume, setVolume] = useState(0.5);
    const turnOn = () => { setToggleVolumeBtn(false); setVolume(0); }
    const turnOff = () => { setToggleVolumeBtn(true); setVolume(0.5); }
    useEffect(() => {
        if (wavesurfer !== null)
            wavesurfer.playPause()
    }, [isPlay])
    useEffect(() => {
        const sibal = WaveSurfer.create({
            container: waveform.current,
            backend: 'MediaElement',
            mediaType: 'audio',
            waveColor: '#828282',
            progressColor: '#BD03FA',
            height: 40,
            barHeight: 3,
            barWidth: 1
        })
        sibal.load(selectedMusic.songFileURI)
        sibal.playPause()
        sibal.on('audioprocess', (wavesurfer) => {
            setRealTime(<Duration seconds={wavesurfer} />);
            setEndTime(<Duration seconds={sibal.getDuration()} />)
        })
        var onChangeVolume = (e) => {
            sibal.setVolume(e.target.value);
        }

        window.addEventListener('input', onChangeVolume);
        sibal.on('finish', () => {
            setFinished(!finished)
            return false
        })

        setWavesurfer(sibal)
        return () => {
            window.removeEventListener('input', onChangeVolume);
        }
    }, []);
    useEffect(() => {
        if (wavesurfer !== null) {
            wavesurfer.load(selectedMusic.songFileURI)
            wavesurfer.playPause()
            wavesurfer.on('audioprocess', (wavesurfer2) => {
                setRealTime(<Duration seconds={wavesurfer2} />);
                setEndTime(<Duration seconds={wavesurfer.getDuration()} />)
            })
            wavesurfer.on('finish', () => {
                setFinished(!finished)
                return false
            })
        }
    }, [selectedMusic]);
    return (
        <Container>
            <InnerContainer>
                <AlbumArt src={selectedMusic.albumArt}></AlbumArt>
                <Column1>
                    <Title>{selectedMusic.title}</Title>
                    <ArtistName>{selectedMusic.artistName}</ArtistName>
                </Column1>
                <PlayButton
                    onClick={() => { toggle(); }}
                    src={isPlay ? pause2 : play2}>
                </PlayButton>
                <ProgressBar>
                    <Time style={{ height: 32, paddingTop: 12, paddingBottom: 4, width: 352 }}>
                        <div>{realTime}</div>
                        <div style={{ marginLeft: 'auto' }}>{endTime}</div>
                    </Time>
                    <div ref={waveform} style={{ width: 352, height: 40 }} />
                </ProgressBar>
                <Icon
                    src={toggleVolumeBtn ? volumeOn : volumeOff} style={{ cursor: 'pointer' }}
                    onClick={() => {
                        toggleVolumeBtn ? turnOn() : turnOff()
                    }}
                />
                <Input style={{ width: 80, cursor: 'pointer' }} ref={volumeForm} type="range" min="0" max="1" step="0.1"
                    value={volume}
                    onChange={(e) => setVolume(e.target.value)}
                />
            </InnerContainer>
        </Container>
    );
}
