import axios from "axios";
import { useEffect, useState } from "react";
import {
    useRecoilValue,
    useRecoilValueLoadable,
    useSetRecoilState,
} from "recoil";
import { Button } from "src/components/button/button";
import { CheckBox } from "src/components/button/checkBox";
import { InputForm } from "src/components/input/inputForm";
import {
    API_SERVER2,
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
    CATEGORIES,
    GENRES,
} from "src/constants";
import { cloneObj, onChange } from "src/lib/lib";
import {
    currentSizeSelector,
    IDropdownState,
    IDropdownStateNode,
} from "src/models";
import { ArtistProfileScrollState } from "src/models/artistProfile/about";
import { artistPortFolioProfileState } from "src/models/artistProfile/artistPortFolio";
import {
    singleArtistPortfolioSelector,
    useUpdatePortfolios,
} from "src/models/artistProfile/profileBanner";
import { modalScrollState } from "src/models/modal";
import {
    FontHeader,
    FontNormal,
    FontRedStar,
    makeCenter,
    SpoqaHansSansNeo,
} from "src/styles/css";
import styled from "styled-components";
import { BlankButton } from "../../components/button/blankButton";
import { Modal, ModalFooter, ModalHeader } from "../../components/modal/modal";
import { getNameForURIFromURI, isEmptyObject } from "../../lib/lib";
import { ChooseGenre } from "./chooseGenre";
import { ChooseService } from "./chooseService";
import { ProjectProfile } from "./projectProfile";
import { VideoColumn } from "./videoColumn";
import { YoutubeModal } from "./youtubeModal";

export const UpdateArtistProject = (id: string) => {
    const [isOpen, setIsOpen] = useState(false);
    const aritstProfile = useRecoilValue<any>(artistPortFolioProfileState);
    const [projectName, setProjectName] = useState("");
    const [withStage8, setWithStage8] = useState(false);
    const [projectStart, setProjectStart] = useState("");
    const [projectEnd, setProjectEnd] = useState("");
    const [clientName, setClientName] = useState("");
    const currentSize = useRecoilValue(currentSizeSelector);

    const [servicesKeywords, setServicesKeywords] = useState<
        IDropdownStateNode[]
    >(new Array<IDropdownStateNode>());
    const [services, setServices] = useState<IDropdownState>(
        cloneObj(CATEGORIES),
    );
    const [genreKeywords, setGenreKeywords] = useState<IDropdownStateNode[]>(
        new Array<IDropdownStateNode>(),
    );
    const [genres, setGenres] = useState<IDropdownState>(cloneObj(GENRES));
    const [description, setDescription] = useState("");
    const [setUpdate] = useUpdatePortfolios();

    const data = useRecoilValueLoadable(singleArtistPortfolioSelector(id));
    const [modalOpen, setModalOpen] = useState(false);
    const [videoID, setVideoID] = useState(null);
    const openModal = videoID => {
        setVideoID(videoID);
        setModalOpen(true);
    };
    const closeModal = () => {
        setModalOpen(false);
    };
    const [videos, setVideos] = useState("");
    const result = () => {
        let _category = new Array<string>();
        servicesKeywords.map(category => {
            _category.push(category.query);
        });
        let _genre = new Array<string>();
        genreKeywords.map(category => {
            _genre.push(category.query);
        });
        const data = new FormData();
        data.append("files", aritstProfile);
        data.append("name", projectName);
        data.append("withStage8", JSON.stringify(withStage8));
        data.append("category", JSON.stringify(_category));
        data.append("year", JSON.stringify([projectStart, projectEnd]));
        data.append("client", clientName);
        data.append("genre", JSON.stringify(_genre));
        data.append("description", description);
        data.append("files", aritstProfile);
        data.append("nameForURI", getNameForURIFromURI());
        const video: string[] = [];
        video.push(videos);
        data.append("videos", JSON.stringify(video));
        return data;
    };

    const setBGscroll = useSetRecoilState(ArtistProfileScrollState);
    const setModalcroll = useSetRecoilState(modalScrollState);
    const ModalOn = () => {
        setIsOpen(true);
        setBGscroll(false);
        setModalcroll(true);
    };
    const ModalOff = () => {
        setIsOpen(false);
        setBGscroll(true);
        setModalcroll(false);
    };
    const [videoData, setVideoData] = useState({
        pass: false,
        message: "",
        requirement: false,
    });
    useEffect(() => {
        if (data.state === "hasValue") {
            const data2 = data.contents.portfolio;

            setDescription(data2.description);
            setProjectName(data2.name);
            setWithStage8(data2.withStage8);
            let _category = new Array<IDropdownStateNode>();

            data2.category.map(singleCategory => {
                CATEGORIES.map(item => {
                    item.contents.find(item2 => {
                        if (item2.query === singleCategory) {
                            item2.activate = true;
                            _category.push(item2);
                        }
                    });
                });
            });
            let _genre = new Array<IDropdownStateNode>();

            data2.genre.map(singleCategory => {
                GENRES.contents.map(item => {
                    if (item.query === singleCategory) {
                        item.activate = true;
                        _genre.push(item);
                    }
                });
            });
            setServicesKeywords(_category);
            setGenreKeywords(_genre);
            setProjectStart(data2.year[0]);
            setProjectEnd(data2.year[1]);
            setClientName(data2.client);
        }
    }, [data.state, data.contents]);
    const validateVideo = e => {
        const value = e.target.value;
        if (value === "") {
            setVideoData({
                pass: false,
                message: "",
                requirement: false,
            });
            return;
        }
        const duplicate = async () => {
            let form = new FormData();
            form.append("videoUrl", value);
            const url = API_SERVER2 + `/auth/check-videourl`;

            const response = await axios.post(url, form, {
                withCredentials: true,
            });
            return response;
        };
        duplicate()
            .then(function (data) {
                setVideoData({
                    pass: true,
                    message: "사용가능한 Youtube url입니다.",
                    requirement: false,
                });
            })
            .catch(er => {
                setVideoData({
                    pass: false,
                    message: "유효하지 않은 Youtube url입니다.",
                    requirement: true,
                });
            });
    };
    return (
        <>
            <Button
                style={{ marginLeft: "auto" }}
                width={170}
                height={40}
                onClick={ModalOn}
            >
                수정하기
            </Button>
            {data.state === "hasValue" && (
                <Modal isOpen={isOpen} empty={false} fullScreen={true}>
                    {isOpen && (
                        <>
                            <ModalHeader />
                            <ModalMain
                                style={{
                                    display: "flex",
                                    justifyContent: "center",
                                }}
                            >
                                <Container>
                                    <UpperRow>
                                        <ProjectProfile
                                            existedProfile={
                                                data.contents.portfolio
                                                    .portfolioImageURI
                                            }
                                        />
                                        <RightColumn>
                                            <InputForm
                                                requirement
                                                header="프로젝트명"
                                                value={projectName}
                                                width={590}
                                                onChange={onChange(
                                                    setProjectName,
                                                )}
                                                explanation="최종 프로젝트 이름을 입력해주세요."
                                                placeholder="아사히 플레이리스트"
                                            />
                                            <Stage8Row>
                                                <CheckBox
                                                    onClick={() =>
                                                        setWithStage8(
                                                            !withStage8,
                                                        )
                                                    }
                                                    check={withStage8}
                                                />
                                                STAGE8을 통해 진행한
                                                프로젝트인가요?
                                            </Stage8Row>
                                            <FontHeader
                                                style={{ marginTop: 40 }}
                                            >
                                                어느 서비스에 해당되나요?
                                                <FontRedStar>*</FontRedStar>
                                            </FontHeader>
                                            <FontNormal>
                                                이 프로젝트는 어느 서비스에
                                                해당되는지 알려주세요.
                                            </FontNormal>
                                            <ChooseService
                                                keywords={servicesKeywords}
                                                setKeywords={
                                                    setServicesKeywords
                                                }
                                                options={services}
                                                setOptions={setServices}
                                            />
                                            <Row>
                                                <FontHeader>
                                                    수행연도
                                                    <FontRedStar>*</FontRedStar>
                                                </FontHeader>
                                                <FontNormal
                                                    style={{ marginBottom: 16 }}
                                                >
                                                    언제 이 프로젝트가
                                                    진행되었는지
                                                    수행연도(YYYY)를
                                                    입력해주세요.
                                                </FontNormal>
                                                <YearsRow>
                                                    <InputForm
                                                        width={200}
                                                        value={projectStart}
                                                        onChange={onChange(
                                                            setProjectStart,
                                                        )}
                                                        placeholder="프로젝트 시작"
                                                    ></InputForm>
                                                    <Dash>-</Dash>
                                                    <InputForm
                                                        width={200}
                                                        value={projectEnd}
                                                        onChange={onChange(
                                                            setProjectEnd,
                                                        )}
                                                        placeholder="프로젝트 완료"
                                                    ></InputForm>
                                                </YearsRow>
                                            </Row>
                                            <Row>
                                                <FontHeader>
                                                    의뢰자(단체)명
                                                    <FontRedStar>*</FontRedStar>
                                                </FontHeader>
                                                <FontNormal
                                                    style={{ marginBottom: 16 }}
                                                >
                                                    프로젝트를 의뢰한 개인 또는
                                                    단체 이름을 입력해주세요.
                                                </FontNormal>
                                                <YearsRow>
                                                    <InputForm
                                                        width={590}
                                                        value={clientName}
                                                        onChange={onChange(
                                                            setClientName,
                                                        )}
                                                        placeholder="아사히 맥주"
                                                    ></InputForm>
                                                </YearsRow>
                                            </Row>
                                            <Row>
                                                <ChooseGenre
                                                    genreKeywords={
                                                        genreKeywords
                                                    }
                                                    setGenreKeywords={
                                                        setGenreKeywords
                                                    }
                                                    genres={genres}
                                                    setGenres={setGenres}
                                                />
                                            </Row>
                                            <Row>
                                                <FontHeader>
                                                    프로젝트 소개
                                                    <FontRedStar>*</FontRedStar>
                                                </FontHeader>
                                                <FontNormal
                                                    style={{ marginBottom: 16 }}
                                                >
                                                    어떤 프로젝트인지
                                                    설명해주세요. 설명이 더
                                                    상세할 수록, 미래의
                                                    의뢰자에게 더 많은 도움을 줄
                                                    수 있습니다.
                                                </FontNormal>
                                                <InputForm
                                                    placeholder="깔끔하게 다 잊고 음악에 파묻히고 싶은,나를 위한 1:1 라이브 콘서트 '아사히 플레이리스트' 참여"
                                                    height={160}
                                                    textarea={true}
                                                    width={590}
                                                    value={description}
                                                    onChange={onChange(
                                                        setDescription,
                                                    )}
                                                ></InputForm>
                                                <InputForm
                                                    marginTop={40}
                                                    marginBottom={140}
                                                    error={
                                                        videoData.requirement
                                                    }
                                                    errorMessage={
                                                        videoData.message
                                                    }
                                                    header="관련동영상링크"
                                                    value={videos}
                                                    width={590}
                                                    onChange={onChange(
                                                        setVideos,
                                                        validateVideo,
                                                    )}
                                                    explanation="관련 동영상 링크를 입력해주세요."
                                                    placeholder="https://www.youtube.com/watch?v=y5pqNsGlEio"
                                                />
                                            </Row>
                                            {!isEmptyObject(
                                                data.contents.portfolio.videos,
                                            ) && (
                                                <>
                                                    <VideoList>
                                                        {data.contents.portfolio.videos.map(
                                                            (video, i) => {
                                                                return (
                                                                    <VideoColumn
                                                                        openModal={
                                                                            openModal
                                                                        }
                                                                        key={i}
                                                                        video={
                                                                            video
                                                                        }
                                                                        i={i}
                                                                        marginRight={
                                                                            undefined
                                                                        }
                                                                        marginBottom={
                                                                            undefined
                                                                        }
                                                                    />
                                                                );
                                                            },
                                                        )}
                                                    </VideoList>
                                                    <YoutubeModal
                                                        currentSize={
                                                            currentSize
                                                        }
                                                        videoID={videoID}
                                                        open={modalOpen}
                                                        close={closeModal}
                                                    />
                                                </>
                                            )}
                                        </RightColumn>
                                    </UpperRow>
                                </Container>
                            </ModalMain>
                            <ModalFooter
                                style={{
                                    display: "flex",
                                    justifyContent: "center",
                                }}
                            >
                                <InnerConatiner2>
                                    <BlankButton
                                        height={40}
                                        width={96}
                                        onClick={() => ModalOff()}
                                    >
                                        취소
                                    </BlankButton>
                                    <Button
                                        rightAlign={true}
                                        height={40}
                                        width={96}
                                        onClick={() => {
                                            ModalOff();
                                            setUpdate(result(), id);
                                        }}
                                    >
                                        {"확인"}
                                    </Button>
                                </InnerConatiner2>
                            </ModalFooter>
                        </>
                    )}
                </Modal>
            )}
        </>
    );
};
const VideoList = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        flex-wrap: wrap;
        display: flex;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        -ms-overflow-style: none;
        overflow-x: scroll;
        -ms-scroll-chaining: none;
        -ms-scroll-snap-type: mandatory;
        scroll-snap-type: mandatory;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
        -ms-scroll-snap-points-x: snapInterval(0%, 10%);
        display: flex;
    }
    margin-top: 24px;
`;
const InnerConatiner2 = styled.div`
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1156px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1100px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 712px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 331px;
    }
    display: flex;
`;
const Container = styled.div``;

const UpperRow = styled.div`
    display: flex;
`;
const Dash = styled.div`
    width: 40px;
    ${makeCenter()}
`;
const YearsRow = styled.div`
    display: flex;
`;
const Row = styled.div`
    margin-top: 40px;
`;
const RightColumn = styled.div`
    width: 590px;
`;
const PlusImage = styled.img``;
const AddButton = styled.div`
    cursor: pointer;
    margin-left: auto;
    display: flex;
`;
const Text2 = styled.div`
    ${makeCenter(0, 1)}
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
margin-left: 8px;
`;
const Stage8Row = styled.div`
    display: flex;
    margin-top: 16px;
    margin-bottom: 40px;
`;
const ModalMain = styled.div`
    ${SpoqaHansSansNeo(16, 20, 400)};
    padding: 24px;
    min-height: 1450px;
`;
