import axios, { AxiosError, AxiosRequestConfig } from "axios";
import { useCallback } from "react";
import { atom, selector, selectorFamily, useSetRecoilState } from "recoil";
import { API_SERVER2 } from "src/constants";
import { getNameForURIFromURI } from "src/lib/lib";
import { GET } from "..";
import { CPageAPIResponse } from "../../types";
import { currentSizeState } from "../currentSize";
import { artistTriggerState } from "./profileBanner";

const ArtistType = {
    performer: "performer",
    producer: "producer",
} as const;

export type ArtistType = typeof ArtistType[keyof typeof ArtistType];

export interface ArtistInfo {
    SNSChannel: {
        facebook: string;
        instagram: string;
        twitter: string;
        youtube: string;
        website: string;
    };
    activeYear: string[];
    artistType: ArtistType[];
    award: string;
    bannerURI: string;
    category: string[];
    createdAt: string[];
    discography: string[];
    email: string[];
    emailPermit: boolean;
    emailVerified: boolean;
    genre: string[];
    introduction: string;
    nameEN: string;
    nameForURI: string;
    photos: string[];
    profileURI: string;
    slogun: string;
    songs: [];
    testimonials: string[];
    userType: string;
    videos: string[];
    members: string[];
}

export interface ArtistAbout {
    followed: boolean;
    onRequest: boolean;
    artist: ArtistInfo;
}

export const artistInfoState = atom({
    key: "artistInfoState",
    default: 1,
});

export const artistInfoSelector = selector({
    key: "artistInfoSelector",
    get: async ({ get }) => {
        get(artistTriggerState);

        const nameForURI = getNameForURIFromURI();

        const res = await GET<ArtistAbout>(
            `/artists/by-name-uri/${nameForURI}`,
        );

        return res;
    },
});

export interface ITestimonialData {
    content: "";
    field: "";
    writer: "";
    _id: "";
}
export interface IArtistPhotosData {
    photos: string[];
}
interface ITestimonial extends CPageAPIResponse {
    data: ITestimonialData;
}
interface IArtistPhotos extends CPageAPIResponse {
    data: IArtistPhotosData;
}
export const testimonialPage = atom({
    key: "testimonialPage",
    default: 1,
});
export const TestimonialTriggerState = atom({
    key: "TestimonialTriggerState",
    default: Date.now(),
});
export const ArtistProfileScrollState = atom({
    key: "ArtistProfileScrollState",
    default: true,
});
export const testimonialSelector = selector({
    key: "testimonialSelector",
    get: async ({ get }) => {
        get(TestimonialTriggerState);
        get(artistTriggerState);
        const nameForURI = getNameForURIFromURI();

        const res = await GET<ITestimonial>(
            `/artists/testimonials/by-name-uri/${nameForURI}?page=${String(
                get(testimonialPage),
            )}`,
        ).catch((reason: AxiosError) => {
            if (reason.response?.status) {
                return undefined;
            }
        });

        return res;
    },
});

const culLimitNum = (currentSize: string) => {
    if (currentSize === "LARGE" || currentSize === "MIDDLE") {
        return 8;
    } else if (currentSize === "SMALL") {
        return 6;
    } else {
        return 9;
    }
};

export const photoSelector = selectorFamily({
    key: "photoSelector",
    get:
        (newPage: number) =>
        async ({ get }) => {
            const nameForURI = getNameForURIFromURI();
            const limit = culLimitNum(get(currentSizeState));
            const res = await GET<CPageAPIResponse>(
                `/artists/${nameForURI}/photos/by-name-uri/page=${String(
                    newPage,
                )}&limit=${limit}`,
            );
            return res;
        },
});

export const artistPhotosPage = atom({
    key: "artistPhotosPage",
    default: 1,
});

export const artistPhotosSelector = selector({
    key: "artistPhotosSelector",
    get: async ({ get }) => {
        get(artistTriggerState);
        const nameForURI = getNameForURIFromURI();
        const res = await GET<IArtistPhotos>(
            `/artists/${nameForURI}/photos/by-name-uri?page=${String(
                get(artistPhotosPage),
            )}`,
        );
        return res;
    },
});
export function usePostArtistProfileState() {
    const setArtistTriger = useSetRecoilState(artistTriggerState);

    const updateSlogun = useCallback(
        (artistProfileState: string, requestMemo?: string) => {
            if (artistProfileState === null) return;

            const nameForURI = getNameForURIFromURI();
            const options: AxiosRequestConfig = {
                method: "POST",
                data: {
                    artistProfileState: artistProfileState,
                    requestMemo: requestMemo,
                },
                url: API_SERVER2 + `/artists/${nameForURI}/artistProfileState`,
                withCredentials: true,
            };
            axios(options).then(() => setArtistTriger(Date.now()));
        },
        [],
    );

    return [updateSlogun];
}
