import React from "react";
import { BREAKPOINT_SMALL } from "src/constants";
import stage8Logo from "src/images/stage8Logo.svg";
import styled from "styled-components";

export function Logo() {
    return (
        <Container>
            <IMG src={stage8Logo} />
        </Container>
    );
}
const IMG = styled.img`
    position: relative;
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 160px;
        left: -6px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 20px;
        width: 90px;
        left: -3px;
    }
`;
const Container = styled.div`
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) {
        height: 32px;
        width: 160px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 20px;
        width: 90px;
    }
`;
