import { useState } from "react";
import { BlankButton } from "src/components/button/blankButton";
import { Button } from "src/components/button/button";
import { InputForm } from "src/components/input/inputForm";
import {
    Modal,
    ModalFooter,
    ModalHeader,
    ModalMain,
} from "src/components/modal/modal";
import { usePostArtistProfileState } from "src/models/artistProfile/about";
import { makeCenter, SpoqaHansSansNeo } from "src/styles/css";
import styled from "styled-components";

export const UpdateRequestMemo = () => {
    const [setter] = usePostArtistProfileState();
    const [isOpen, setIsOpen] = useState(false);
    const [input, setInput] = useState("");
    const onChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
        setInput(e.target.value);
    };
    return (
        <>
            <Button
                marginRight={30}
                width={200}
                height={60}
                onClick={() => setIsOpen(true)}
            >
                보완요청
            </Button>
            <Modal isOpen={isOpen} empty={false}>
                {isOpen && (
                    <>
                        <ModalHeader>{"메모 추가"}</ModalHeader>
                        <ModalMain>
                            <InputForm
                                onKeyPress={() => {}}
                                style={{ marginBottom: 48 }}
                                onChange={onChange}
                                errorMessage={"*등록되지않은 이메일입니다."}
                                value={input}
                                placeholder="내용"
                                header="보완요청사항"
                            ></InputForm>
                        </ModalMain>
                        <ModalFooter>
                            <BlankButton
                                height={40}
                                width={96}
                                onClick={() => {
                                    setIsOpen(false);
                                }}
                            >
                                취소
                            </BlankButton>
                            <Button
                                rightAlign={true}
                                height={40}
                                width={96}
                                onClick={() => setter("request", input)}
                            >
                                {"확인"}
                            </Button>
                        </ModalFooter>
                    </>
                )}
            </Modal>
        </>
    );
};
const Text2 = styled.div`
    ${makeCenter(0, 1)}
    ${SpoqaHansSansNeo(16, 20, 700, "#BD03FA")}
margin-left: 8px;
`;
const PlusImage = styled.img``;
const AddButton = styled.div`
    height: 24px;
    cursor: pointer;
    margin-left: auto;
    display: flex;
`;
