import axios, { AxiosRequestConfig } from "axios";
import { useCallback } from "react";
import { atom, selector, useSetRecoilState } from "recoil";
import { API_SERVER2 } from "src/constants";
import { CPageAPIResponse } from "src/types";
import { GET } from "..";
import { ArtistType } from "../artistProfile/about";

export const userListTriggerState = atom({
    key: "userListTriggerState",
    default: Date.now(),
});
export interface IUser {
    artisttype: ArtistType[];
    createdAt: string;
    email: string;
    emailPermit: boolean;
    phoneNumber: string;
    realName: string;
    smallProfileURI: string;
    textPermit: boolean;
    userType: string;
    nameForURI: string;
    signature: boolean;
    userCanSee: boolean;
}
interface IUsers {
    users: IUser[];
}
interface IAllUsersData extends CPageAPIResponse {
    data: IUsers;
}
export const allusersSelector = selector({
    key: "alluserSelector",
    get: async ({ get }) => {
        get(userListTriggerState);
        const page = get(userListPageState);
        const filter = get(userListFilterState);
        const search = get(userListSearchState);
        const search2 = get(userListSearchState2);
        const search3 = get(userListSearchState3);

        let url = `/users/all?page=${page}`;

        if (filter !== "") {
            url += `&filterBy=${filter}`;
        }

        if (search !== "") {
            url += `&realName=${search}`;
        } else if (search2 !== "") {
            url += `&email=${search2}`;
        } else if (search3 !== "") {
            url += `&phoneNumber=${search3}`;
        }

        const res = await GET<IAllUsersData>(encodeURI(url));

        return res;
    },
});
export const userListSearchState = atom({
    key: "userListSearchState",
    default: "",
});
export const userListSearchState2 = atom({
    key: "userListSearchState2",
    default: "",
});
export const userListSearchState3 = atom({
    key: "userListSearchState3",
    default: "",
});
export const userListFilterState = atom({
    key: "userListFilterState",
    default: "",
});
export const userListPageState = atom({
    key: "userListPageState",
    default: 1,
});

export function usePostSignature() {
    const setTrigger = useSetRecoilState(userListTriggerState);

    const updateSlogun = useCallback((check: boolean, email: string) => {
        if (check === null) return;

        const options: AxiosRequestConfig = {
            method: "POST",
            data: {
                check: check,
            },
            url: API_SERVER2 + `/artists/${email}/signature`,
            withCredentials: true,
        };
        axios(options).then(() => setTrigger(Date.now()));
    }, []);

    return [updateSlogun];
}

export function usePostUserCanSee() {
    const setTrigger = useSetRecoilState(userListTriggerState);

    const updateSlogun = useCallback((check: boolean, email: string) => {
        if (check === null) return;

        const options: AxiosRequestConfig = {
            method: "POST",
            data: {
                check: check,
            },
            url: API_SERVER2 + `/artists/${email}/usercansee`,
            withCredentials: true,
        };
        axios(options).then(() => setTrigger(Date.now()));
    }, []);

    return [updateSlogun];
}
