import axios from "axios";
import { useHistory } from "react-router";
import {
    BREAKPOINT_LARGE,
    BREAKPOINT_MIDDLE,
    BREAKPOINT_SMALL,
} from "src/constants/common";
import { IImage } from "src/interfaces/common";
import { FontNormal, makeCenter } from "src/styles/css";
import styled from "styled-components";
import { API_SERVER2 } from "../../constants/common";

export function NavBar() {
    const history = useHistory();
    const isLoing = window.localStorage.getItem("isLogin");
    return (
        <Container>
            <InnerContainer>
                {/* <Logo src={stage8Logo} /> */}
                <FontNormal
                    style={{ cursor: "pointer" }}
                    onClick={() => history.push(`/usermanagement`)}
                >
                    유저 관리
                </FontNormal>
                {isLoing ? (
                    <FontNormal
                        style={{ cursor: "pointer", marginLeft: 40 }}
                        onClick={async () => {
                            const url = API_SERVER2 + `/auth/logout`;
                            await axios
                                .post(url, {}, { withCredentials: true })
                                .then(() => {
                                    window.localStorage.removeItem("isLogin");
                                    window.localStorage.removeItem("userData");
                                    history.push("/");
                                    window.scrollTo(0, 0);
                                });
                        }}
                    >
                        로그아웃
                    </FontNormal>
                ) : (
                    <FontNormal
                        style={{ cursor: "pointer", marginLeft: 40 }}
                        onClick={() => history.push(`/`)}
                    >
                        로그인
                    </FontNormal>
                )}
            </InnerContainer>
        </Container>
    );
}
const Logo = styled.img<IImage>`
    cursor: pointer;
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) {
        width: 200px;
        height: 40px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 146px;
        height: 30px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 92px;
        height: 20px;
        margin-right: 16px;
    }
`;
const Container = styled.div`
    z-index: 9999;
    position: fixed;
    width: 100%;
    background-color: #0e0e10;
    ${makeCenter(1)};
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        height: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        height: 80px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        height: 56px;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        height: 56px;
    }
`;
const InnerContainer = styled.div`
    transition: all 0.5s;
    ${makeCenter()};
    padding: 0px 20px;
    @media screen and (min-width: ${BREAKPOINT_LARGE}px) {
        width: 1196px;
    }
    @media screen and (min-width: ${BREAKPOINT_MIDDLE}px) and (max-width: ${BREAKPOINT_LARGE}px) {
        width: 1140px;
    }
    @media screen and (min-width: ${BREAKPOINT_SMALL}px) and (max-width: ${BREAKPOINT_MIDDLE}px) {
        width: 100%;
    }
    @media screen and (max-width: ${BREAKPOINT_SMALL}px) {
        width: 100%;
    }
`;
